import angular from 'angular';

let app = angular.module('cachePool', [])
  .factory('cachePool', function () {

      var fetchItem = function (key) {
          if (!key) {
              return null;
          }

          var itemStr = localStorage.getItem('Neo_' + key),
              item;

          try {
              item = JSON.parse(itemStr);
          } catch (e) {}

          if (!item) {
              return null;
          }

          return item;
      };

      var ONE_DAY = 24 * 3600 * 1000;

      var storageData = {

          /**
           * 
           * @param key name
           * @param data 
           * @param expires ()()
           */
          push: function (key, data, expires) {
              if (!key || !data) {
                  return;
              }

              var item = fetchItem(key) || {};

              item.value = data || undefined; //value

              item.expired = expires ? (Date.now() + ONE_DAY * expires) : undefined;

              localStorage.setItem('Neo_' + key, JSON.stringify(item));
          },

          /**
           * 
           * @param key name
           * expired
           */
          pull: function (key) {
              var item = fetchItem(key),
                  data;

              if (!item || item.expired && item.expired <= Date.now()) {
                  return null;
              } else {
                  data = item['value'];
              }

              return data;
          },

          /**
           * 
           * @params:
           *     key: name
           *     dataKey: 
           */
          remove: function (key, dataKey) {
              if (!key) {
                  return;
              }

              var item = fetchItem(key);

              if (!item) {
                  localStorage.removeItem('Neo_' + key);
                  return;
              }

              if (dataKey && item[dataKey]) {
                  item[dataKey] = undefined;
                  localStorage.setItem('Neo_' + key, JSON.stringify(item));
              } else {
                  localStorage.removeItem('Neo_' + key);
              }

          },

          /**
           * 
           * @params:
           *     key: name
           *     dataKey:   { a:1 }
           */
          modify: function (key, dataKey) {
              if (!key) {
                  return;
              }

              var items = fetchItem(key),
                  i;

              if (!items) {
                  localStorage.removeItem('Neo_' + key);
                  return;
              }

              if (dataKey) {
                  for (i in items.value) {
                      if (dataKey[i]) {
                          items.value[i] = dataKey[i];
                      }
                  }

                  localStorage.setItem('Neo_' + key, JSON.stringify(items));
              }
          }

      };

      return storageData;
  });


export default app;  


