import DataService from 'dataService';
import io from 'socket.io-client';

class Controller {
  constructor($scope, $rootScope, $state, $timeout, cachePool) {
    this.$scope = $scope;
    this.$state = $state;
    this.userData = cachePool.pull('user');
    this.$scope.items = [];

    this.getFirstData();
    this.socket();
    this.top();
  }
  /**
   * socket
   */
  socket() {
    DataService.get('/zhubo/message-server')
      .then((data) => {
        if(data.code === 1){
          let ip = this.getRandomNumber(data.data),
            userID = (this.userData.is_login ? 0 : this.userData.uid),
            client = `chat_${this.userData.lid}_${this.userData.gid}_${this.userData.id}_${userID}`,
            chatData = {"room": "CHAT_CASINO", "roomid": this.userData.id, "type":"other", "gid": this.userData.gid,"pid":0,"direc":"next"};  

          let socketUrl = 'http://'+ ip +'/nba_v1?client=' + client + '&background=false&type=5';

          let socket = io.connect(socketUrl);

          socket.on('connect', () => {
            socket.emit('join', chatData);
          });

          // 
          socket.on('wall', data => {
            // 
            this.$scope.$emit('onlinePeopleNumber', data.online);

            if(!data.result.data || !data.result.data.length || data.result.data.length >= 2){
              return;
            }
    
            this.upload(data.result.data);
          })

          // 
          socket.on('error', function (data) {
          })
        }
      });
  }
  upload(data) {
    if(!angular.isUndefined(data[0].event_type) && data[0].event_type === 2002) {
      this.$state.go('app.error', {page: 1001});
      return;
    }

    this.top();

    this.$scope.$apply(() => {
      data = data.concat(this.$scope.items);
      this.$scope.items = data;
    })
  }
  /**
   * 
   * @param  {[type]} params [description]
   * @return {[type]}        [description]
   */
  getFirstData() {
    DataService.get('/zhubo/latest')
      .then((data) => {
        if(data.code === 1) {
          this.$scope.$apply(() => {
            let items = [...data.data.message, ...data.data.gift];
            this.$scope.items = items;
          })
        }
      });
  }
  /**
   * 
   * @return {[type]} [description]
   */
  top() {
    DataService.get('/zhubo/ranking')
      .then((data) => {
        if(data.code === 1) {
          this.$scope.$apply(() => {
            this.$scope.tops = data.data.room;
          })
        }
      });
  }
  /**
   * 
   * @param  {[type]} arr [description]
   * @return {[type]}     [description]
   */
  getRandomNumber(arr){
    var len = arr.length,
        count = Math.floor(Math.random() * len);

    return arr[count];
  }
}

Controller.$inject = ['$scope', '$rootScope', '$state', '$timeout', 'cachePool'];

export default Controller;



